import Block from "./Block"
import Vector from "./Vector"

class Camera {
    private _position: Vector
    private velocity: Vector
    private acceleration: Vector
    private inertia: number
    private friction: number
    private map: Array<Array<Block>>

    constructor(map: Array<Array<Block>>) {
        this._position = new Vector(0, 0)
        this.velocity = new Vector(0, 0)
        this.acceleration = new Vector(0, 0)
        this.inertia = 1
        this.friction = 0.9
        this.map = map
        this.startCameraMovementEvent()
    }

    public get position(): Vector {
        return this._position
    }

    public set position(newPosition: Vector) {
        this._position = newPosition
        this.map.forEach(x => {
            x.forEach(block => {
                block.positionRelativeToScene = Vector.add(block.position, this._position)
            })
        })
    }

    private addForce(force: Vector): void {
        this.acceleration.add(Vector.mult(1 / this.inertia, force))
    }

    public updateMovement(): void {
        this.position = Vector.add(this.position, this.velocity)
        this.velocity.add(this.acceleration)
        this.velocity.mult(this.friction)
        this.acceleration.zero()
    }

    private startCameraMovementEvent() {
        document.addEventListener("keydown", (e) => {
            const key = e.key.toLocaleLowerCase()

            if (key === 'w') {
                this.addForce(new Vector(0, 10))
            }
            else if (key === 's') {
                this.addForce(new Vector(0, -10))
            }
            else if (key === 'd') {
                this.addForce(new Vector(-10, 0))
            }
            else if (key === 'a') {
                this.addForce(new Vector(10, 0))
            }
        })
    }
}

export default Camera
