import Block from "./Block"
import Vector from "./Vector"
import Camera from "./Camera"
import { 
    TILE_SIZE, 
    BLOCK_DEFAULT_SIZE,
    SPRITE_NAME_TO_SPRITE_POSITION_IN_TILE_MAP
 } from "./constants"

function generateMap(mapSize: number) {
    const map = []
    for (let y = 0; y < mapSize; y++) {
        map[y] = []
        for (let x = 0; x < mapSize; x++) {
            map[y][x] = null
        }
    }
    return map
}

class Scene {
    public map: Array<Array<Block>>
    private context: CanvasRenderingContext2D

    public onClick: Function
    private _blockSelected: Block | null
    public tilemap: HTMLImageElement
    private camera: Camera
    public canGoOnWater: boolean

    constructor(context: CanvasRenderingContext2D, mapSize: number, tilemap: HTMLImageElement) {
        this.context = context
        this.map = generateMap(mapSize)
        this.blockSelected = null
        this.camera = new Camera(this.map)
        this.tilemap = tilemap
        this.startListeningToMouseEvents()
        this.canGoOnWater = false
    }

    public get blockSelected(): Block | null {
        return this._blockSelected
    }

    public set blockSelected(value: Block | null) {    
        this._blockSelected = value
        if (this._blockSelected) {
            if (this._blockSelected.type[0] == "T") {
                this.canGoOnWater = false
            }
            else if (this._blockSelected.type == "SHIP") {
                this.canGoOnWater = true
            }
        }
    }

    public add(block: Block, x: number, y: number): void {
        this.map[y][x] = block
    }

    public update(): void {
        this.camera.updateMovement()
        this.render()
    }

    private renderSelectedBlock() {
        if (this.blockSelected !== null) {
            const blockPositionRelativeToSceneX = this.camera.position.x + BLOCK_DEFAULT_SIZE * this.blockSelected.x
            const blockPositionRelativeToSceneY = this.camera.position.y + BLOCK_DEFAULT_SIZE * this.blockSelected.y 
            this.context.drawImage(
                this.tilemap,
                0,
                5 * TILE_SIZE,
                TILE_SIZE,
                TILE_SIZE,
                blockPositionRelativeToSceneX + 5,
                blockPositionRelativeToSceneY + 5,
                BLOCK_DEFAULT_SIZE - 10,
                BLOCK_DEFAULT_SIZE - 10
            )
        }
        if (this.canGoOnWater) {
            const blockPositionRelativeToSceneX = this.camera.position.x + BLOCK_DEFAULT_SIZE * this.blockSelected.x
            const blockPositionRelativeToSceneY = this.camera.position.y + BLOCK_DEFAULT_SIZE * this.blockSelected.y 
            this.context.drawImage(
                this.tilemap,
                12 * TILE_SIZE,
                7 * TILE_SIZE,
                TILE_SIZE,
                TILE_SIZE,
                blockPositionRelativeToSceneX + 5,
                blockPositionRelativeToSceneY + 5,
                BLOCK_DEFAULT_SIZE - 10,
                BLOCK_DEFAULT_SIZE - 10
            )

            
            this.context.fillStyle = "#FFFFFF"
            this.context.strokeStyle = "#555"
            this.context.lineWidth = 7
            this.context.font = "20px 'Press Start 2P'"
            this.context.strokeText(
                String(this.blockSelected.population), 
                blockPositionRelativeToSceneX + BLOCK_DEFAULT_SIZE / 2 - 30, 
                blockPositionRelativeToSceneY + BLOCK_DEFAULT_SIZE / 2 - 20
            )
            this.context.font = "20px 'Press Start 2P'"
            this.context.fillText(
                String(this.blockSelected.population), 
                blockPositionRelativeToSceneX + BLOCK_DEFAULT_SIZE / 2 - 30, 
                blockPositionRelativeToSceneY + BLOCK_DEFAULT_SIZE / 2 - 20
            )
        

        }
    }

    public eachBlock(fn: Function) {
        this.map.forEach(x => {
            x.forEach(block => {
                fn(block)
            })
        })
    }

    loadMap(map: Array<Array<string>>) {
        for (let y = 0; y < map.length; y++) {
            for (let x = 0; x < map.length; x++) {
                const size = new Vector(BLOCK_DEFAULT_SIZE + 1, BLOCK_DEFAULT_SIZE + 1)
                const position = new Vector(x * BLOCK_DEFAULT_SIZE, y * BLOCK_DEFAULT_SIZE)
                const block = new Block(position, size, x, y, map[y][x])
                block.tilemap = this.tilemap

                if (map[y][x][0] == "W") {
                    block.canBeConquer = false
                }
                

                if (map[y][x] == "SHIP") {
                    block.population = 2
                    block.canBeConquer = true
                }
                else if (map[y][x] === "TOWER_LEVEL_1") {
                    block.population = 10
                }

                else if (map[y][x] === "MOUNTAIN") {
                    block.canBeConquer = false
                }                

                // Set sprite position in tilemap for eatch block
                const spriteNames = Object.keys(SPRITE_NAME_TO_SPRITE_POSITION_IN_TILE_MAP)
                spriteNames.forEach(name => {
                    if (map[y][x] == name) { 
                        block.spritePosition = SPRITE_NAME_TO_SPRITE_POSITION_IN_TILE_MAP[name]
                        if (SPRITE_NAME_TO_SPRITE_POSITION_IN_TILE_MAP[name + "_L2"]) {
                            block.secondLayerSpritePosition = SPRITE_NAME_TO_SPRITE_POSITION_IN_TILE_MAP[name + "_L2"]
                        }
                    }
                })

                this.add(block, x, y)
            }
        }
    }

    // Events
    public startListeningToMouseEvents(): void {
        document.addEventListener("click", (e: MouseEvent) => {
            const mousePosition = new Vector(e.clientX, e.clientY)
            this.onClick(this)
            this.map.forEach(x => {
                x.forEach(block => {
                    if (
                        (mousePosition.x > block.positionRelativeToScene.x && mousePosition.x < block.positionRelativeToScene.x + block.size.x)
                        &&
                        (mousePosition.y > block.positionRelativeToScene.y && mousePosition.y < block.positionRelativeToScene.y + block.size.y)
                    ) {
                        block.onClick(this.map)
                    }
                })
            })
        })
    }

    public saveSelectedUnitsInShip(block: Block) {
        block.type = "BOAT_LEFT"
    }

    // Renderization
    public render(): void {
        this.clear()
        this.map.forEach(x => {
            x.forEach(block => {
                block.render(this.context)
            })
        })
        this.renderSelectedBlock()
    }

    private clear(): void {
        this.context.beginPath()
        this.context.fillStyle = "#000000"
        this.context.fillRect(0, 0, window.innerWidth, window.innerHeight)
    }
}

export default Scene
