import Player from "./Player"
import Vector from "./Vector"

const TILE_SIZE = 16
const BLOCK_LINE_WIDTH = 5

class Block {
    private _position: Vector
    private _size: Vector
    public positionRelativeToScene: Vector
    public onClick: Function

    public spritePosition: Vector
    public secondLayerSpritePosition: Vector
    public tilemap: HTMLImageElement
    public color: string
    public canBeConquer: boolean
    private _population: number

    private _owner: Player
    private _x: number
    private _y: number

    public type: string

    constructor(position: Vector, size: Vector, x: number, y: number, type: string) {
        this._x = x
        this._y = y
        this._position = position
        this._size = size
        this.positionRelativeToScene = this._position
        this.color = ""
        this.canBeConquer = true
        this._population = 0
        this.secondLayerSpritePosition = new Vector(0, 0)
        this.type = type
    }

    public get population(): number {
        return this._population
    }
    public set population(value: number) {
        this._population = value
    }

    public get x(): number {
        return this._x
    }

    public get y(): number {
        return this._y
    }

    public get position(): Vector {
        return this._position
    }

    public get size(): Vector {
        return this._size
    }

    public get owner(): Player {
        return this._owner
    }

    public set owner(value: Player) {
        if (this._owner !== undefined) {
            this._owner.removeBlock(this)
        }
        this._owner = value
        this.color = this._owner.color
    }

    // Can be change if it's decided that the rendering tech should change
    public render(context: CanvasRenderingContext2D): void {
        context.beginPath()
    
        context.drawImage(
            this.tilemap,
            this.spritePosition.x * TILE_SIZE,
            this.spritePosition.y * TILE_SIZE,
            TILE_SIZE,
            TILE_SIZE,
            this.positionRelativeToScene.x,
            this.positionRelativeToScene.y,
            this._size.x,
            this._size.y
        )

        if (this.color.length) {
            context.fillStyle = this.color
            context.rect(
                this.positionRelativeToScene.x, 
                this.positionRelativeToScene.y, 
                this._size.x, 
                this._size.y
            )
            context.fill()
        }

        if (this.secondLayerSpritePosition.x) {
            context.drawImage(
                this.tilemap,
                this.secondLayerSpritePosition.x * TILE_SIZE,
                this.secondLayerSpritePosition.y * TILE_SIZE,
                TILE_SIZE,
                TILE_SIZE,
                this.positionRelativeToScene.x,
                this.positionRelativeToScene.y,
                this._size.x,
                this._size.y
            )   
        }

        if (this.canBeConquer) {
            if (this.population !== 0) {
                context.fillStyle = "#FFFFFF"
                context.strokeStyle = "#555"
                context.lineWidth = 7
                context.font = "20px 'Press Start 2P'"
                context.strokeText(
                    String(this.population), 
                    this.positionRelativeToScene.x + this._size.x / 2 - 30, 
                    this.positionRelativeToScene.y + this._size.y / 2 - 20
                )
                context.font = "20px 'Press Start 2P'"
                context.fillText(
                    String(this.population), 
                    this.positionRelativeToScene.x + this._size.x / 2 - 30, 
                    this.positionRelativeToScene.y + this._size.y / 2 - 20
                )
            }
        }

        context.strokeStyle = "rgba(255, 255, 255, 0.1)"
        context.lineWidth = BLOCK_LINE_WIDTH
        context.strokeRect(
            this.positionRelativeToScene.x + BLOCK_LINE_WIDTH / 2, 
            this.positionRelativeToScene.y + BLOCK_LINE_WIDTH / 2, 
            this._size.x - BLOCK_LINE_WIDTH, 
            this._size.y - BLOCK_LINE_WIDTH
        )
    }

}

export default Block
